import React from "react";

const AboutUsDesktopLazy = React.lazy(() => import("./AboutUsDesktop"));
const AboutUsMobileLazy = React.lazy(() => import("./AboutUsMobile"));

const AboutUs = ({ isMobile, scrollY }) => {
  return (
    <>
      {isMobile ? (
        <AboutUsMobileLazy scrollY={scrollY} />
      ) : (
        <AboutUsDesktopLazy scrollY={scrollY} />
      )}
    </>
  );
};

export default AboutUs;
